export function debounce(callback: () => void, delay: number): () => void {
  let previousTimer: number | null = null;
  let lastCallTimestamp = 0;
  function wrapper(): void {
    const currentTimestamp = Date.now();
    if (previousTimer && (currentTimestamp - lastCallTimestamp <= delay)) {
      window.clearTimeout(previousTimer);
    }
    lastCallTimestamp = currentTimestamp;
    previousTimer = window.setTimeout(callback, delay);
  }

  return wrapper;
}

export function throttle(func: () => void, timeout: number): () => void {
  let skipped = false;

  function wrapper(): void {
    if (skipped) {
      return;
    }

    func();
    skipped = true;
    setTimeout(() => {
      skipped = false;
    }, timeout);
  }

  return wrapper;
}

export default null;
