import Vue from 'vue';
import Vuex, { Store, StoreOptions } from 'vuex';
import { SsrApi } from '@/app/website/client/type/ssr-api';
import { MutationType } from '@/app/website/client/store/mutation/type';
import { RawTileData } from '@/app/shared/client/type/tile';
import { parseRawSiteData, parseRawTileData } from '@/app/shared/client/helper/tile';
import { UpdateTileListMutation } from '@/app/website/client/store/mutation/update-tile-list';
import { WebsiteRuntimeContext } from '@/app/website/client/type/website-runtime-context';
import { InitContextMutation } from '@/app/website/client/store/mutation/init-context';
import { SetProfileMutation } from '@/app/website/client/store/mutation/set-profile';
import { Bucket } from '@/app/shared/client/type/bucket';
import createGetters from '@/app/website/client/store/getters';
import { Profile } from '@/app/shared/client/type/profile';
import { Page } from '@/app/website/client/type/page';
import { State } from './state';
import createActions from './actions';
import createMutations from './mutations';

Vue.use(Vuex);

interface StoreDeclaration {
  store: Store<State>;
  api: SsrApi;
}

const initialState: State = {
  context: {
    previewMode: false,
    ssrRendered: false,
    webpSupported: false,
    bucketUrls: {
      [Bucket.US_VIR]: undefined,
      [Bucket.EU_FRA]: undefined,
      [Bucket.AU_SYD]: undefined,
    },
    translations: {},
    heapScriptUrl: undefined,
    page: Page.MAIN,
    appJsUrls: undefined,
    appCssUrls: undefined,
  },
  profile: {
    siteId: 0,
    storeName: '',
  },
  tileList: [],
  siteSettings: undefined,
  focusedTileId: undefined,
  imageListInUploadingState: [],
  burgerMenuOpened: false,
};

function createStoreOptions(state: State): StoreOptions<State> {
  return {
    state,
    mutations: createMutations(),
    actions: createActions(),
    getters: createGetters(),
  };
}

function init(
  context: WebsiteRuntimeContext,
  profile: Profile,
  rawTileList: RawTileData[],
  store: Store<State>,
): Promise<State> {
  return new Promise((resolve, reject) => {
    try {
      const contextPayload = { context };
      store.commit<InitContextMutation>({
        type: MutationType.INIT_CONTEXT,
        payload: contextPayload,
      });

      store.commit<SetProfileMutation>({
        type: MutationType.SET_PROFILE,
        payload: {
          profile,
        },
      });

      const globalSettings = rawTileList
        .find(({ type }) => type === 'GLOBAL');

      const tileDataList = rawTileList
        .filter(({ type }) => type !== 'GLOBAL')
        .map(parseRawTileData);

      const tileListPayload = { tileList: tileDataList };
      store.commit<UpdateTileListMutation>({
        type: MutationType.UPDATE_TILE_LIST,
        payload: tileListPayload,
      });
      store.commit(MutationType.UPDATE_SITE_SETTINGS, parseRawSiteData(globalSettings));

      resolve(store.state);
    } catch (error) {
      reject(error);
    }
  });
}

function dehydrateFunction(
  store: Store<State>,
): (
  context: WebsiteRuntimeContext,
  profile: Profile,
  rawTileList: RawTileData[],
) => Promise<State> {
  return (
    context: WebsiteRuntimeContext,
    profile: Profile,
    rawTileList: RawTileData[],
  ): Promise<State> => init(context, profile, rawTileList, store);
}

function hydrateFunction(store: Store<State>): (state: State) => void {
  return (value: State): void => {
    store.replaceState(value);
  };
}

function createApi(store: Store<State>): SsrApi {
  return {
    dehydrate: dehydrateFunction(store),
    hydrate: hydrateFunction(store),
  };
}

export default function createStore(): StoreDeclaration {
  const store = new Store(createStoreOptions(initialState));
  const api = createApi(store);
  return { store, api };
}
