import initApi from '@/app/preview/client/api';
import preconfigure from './pre-configuration';
import createApp from './index';

function runApp(webpSupported: boolean): void {
  preconfigure();
  const { app, store } = createApp(webpSupported);
  initApi(store);
  app.$mount('#ec-instantsite');
}
window.previewApi.runApp = runApp;
