import { Ref } from '@vue/composition-api';
import { ViewportSettings } from '@/app/shared/client/type/settings/viewport-settings';
import { AppearanceEffectType } from '@/app/shared/client/type/settings/general-settings';

export function checkIfTileShown(
  isTileShowRef: Ref<boolean>,
  viewportSettings: ViewportSettings,
  windowScrollTop?: number,
  tileOffsetTop?: number,
  tileOffsetHeight?: number,
): boolean {
  if (tileOffsetHeight && !isTileShowRef.value) {
    const currentOffsetTop = tileOffsetTop ?? 0;
    const currentOffsetHeight = tileOffsetHeight ?? 0;
    const currentScrollTop = windowScrollTop ?? 0;
    const currentViewportHeight = viewportSettings.height ?? 0;

    // Если блок занимает нижнюю треть экрана или если на экране половина блока
    if (currentScrollTop + currentViewportHeight * 0.7 > currentOffsetTop
      || currentScrollTop + currentViewportHeight > currentOffsetTop + currentOffsetHeight * 0.5) {
      isTileShowRef.value = true;
    }
  }

  return isTileShowRef.value;
}

export function getLayoutAppearanceEffectClass(
  appearanceEffect?: AppearanceEffectType,
): string {
  switch (appearanceEffect) {
    case AppearanceEffectType.FADE_IN:
      return 'ins-tiles--fade-in';
    case AppearanceEffectType.FADE_IN_UP:
      return 'ins-tiles--fade-in-up';
    default:
      return '';
  }
}

export default {};
