import { Ref, watch } from '@vue/composition-api';
import { isEUorGB, loadScript } from '@/app/website/client/helper';

const GOOGLE_UNIVERSAL_ANALYTICS_ID_REGEXP = /^([a-zA-Z]{2}-\d+-\d+)$/;

function loadTrackingScript(): Promise<Event> {
  return loadScript('https://www.google-analytics.com/analytics.js');
}

function createTracking(
  trackingId: string,
  trackingConsentEnabled: boolean,
  trackingAllowed: boolean,
  countryCode: string | undefined,
  initial: boolean,
): void {
  const { ga } = window;
  if (ga) {
    if (!initial) {
      ga('remove');
    }

    const trackingOptions = trackingAllowed ? 'auto' : { storage: 'none' };
    ga('create', trackingId, trackingOptions);
    ga('require', 'ec');
    if (trackingAllowed) {
      ga('require', 'displayfeatures');
    }
    const anonymizeIp = trackingConsentEnabled || isEUorGB(countryCode);
    ga('set', 'anonymizeIp', anonymizeIp);
    ga('set', 'page', '/');
  }
}

function sendPageViewEvent(): void {
  const { ga } = window;
  if (ga) {
    ga('send', 'pageview');
  }
}

function processTracking(
  trackingId: string,
  trackingConsentEnabled: Ref<boolean>,
  trackingAllowed: Ref<boolean>,
  countryCode: string | undefined,
): void {
  watch([trackingConsentEnabled, trackingAllowed], (newValues, oldValues) => {
    const [newTrackingConsentEnabled, newTrackingAllowed] = newValues;
    const [oldTrackingConsentEnabled, oldTrackingAllowed] = oldValues ?? [];

    if (newTrackingConsentEnabled === oldTrackingConsentEnabled
      && newTrackingAllowed === oldTrackingAllowed) {
      return;
    }

    const initial = oldTrackingConsentEnabled === undefined && oldTrackingAllowed === undefined;

    createTracking(
      trackingId,
      newTrackingConsentEnabled,
      newTrackingAllowed,
      countryCode,
      initial,
    );
  }, { immediate: true });

  sendPageViewEvent();
}

function isGoogleUniversalAnalyticsId(trackingId: string) : boolean {
  return GOOGLE_UNIVERSAL_ANALYTICS_ID_REGEXP.test(trackingId);
}

export function initTracking(
  trackingId: string | undefined,
  trackingConsentEnabled: Ref<boolean>,
  trackingAllowed: Ref<boolean>,
  countryCode: string | undefined,
): void {
  if (trackingId == null || !isGoogleUniversalAnalyticsId(trackingId)) {
    return;
  }

  loadTrackingScript()
    .then(() => {
      processTracking(trackingId, trackingConsentEnabled, trackingAllowed, countryCode);
    });
}

export default {};
