export function isNullOrEmpty(value?: string): value is undefined {
  return !value;
}

export function isNotNullOrEmpty(value?: string): value is string {
  return !isNullOrEmpty(value);
}

export function range(start: number, end: number): number[] {
  const length = end - start;
  return Array.from({ length }, (_, index) => start + index);
}

export type FieldName<T> = Extract<keyof T, string>
export type FieldTypes<T> = T[FieldName<T>]
export const nameOf = <T>(name: FieldName<T>): string => name;

export function omitObjectUndefinedFields<T>(data: unknown): T {
  return JSON.parse(JSON.stringify(data));
}
