import { MutationPayload } from 'vuex';
import { State } from '@/app/website/client/store/state';
import { MutationType } from '@/app/website/client/store/mutation/type';
import { ImageInUploadingState } from '@/app/shared/client/type/image-in-uploading-state';

interface SetUploadingImageListMutationPayload {
  imageListInUploadingState: ImageInUploadingState[];
}

export interface SetUploadingImageListMutation extends MutationPayload {
  type: MutationType.SET_UPLOADING_IMAGE_LIST;
  payload: SetUploadingImageListMutationPayload;
}

export default function setUploadingImageListMutation(
  oldState: State,
  data: SetUploadingImageListMutation,
): State {
  const newState = oldState;
  newState.imageListInUploadingState = data.payload.imageListInUploadingState;
  return newState;
}
