import { MutationTree } from 'vuex';
import { MutationType } from '@/app/website/client/store/mutation/type';
import updateTileListMutation from '@/app/website/client/store/mutation/update-tile-list';
import setFocusedTileMutation from '@/app/website/client/store/mutation/set-focused-tile-id';
import updateSiteSettingsMutation from
  '@/app/website/client/store/mutation/update-site-settings-mutation';
import initContextMutation from '@/app/website/client/store/mutation/init-context';
import updateSiteContextMutation from '@/app/website/client/store/mutation/update-site-context';
import setUploadingImageListMutation
  from '@/app/website/client/store/mutation/set-uploading-image-list';
import setBurgerMenuOpenStateMutation from '@/app/website/client/store/mutation/set-burger-menu-open-state';
import setProfileMutation from '@/app/website/client/store/mutation/set-profile';
import { State } from './state';

const mutations = {
  [MutationType.INIT_CONTEXT]: initContextMutation,
  [MutationType.SET_PROFILE]: setProfileMutation,
  [MutationType.UPDATE_TILE_LIST]: updateTileListMutation,
  [MutationType.SET_FOCUSED_TILE_ID]: setFocusedTileMutation,
  [MutationType.SET_UPLOADING_IMAGE_LIST]: setUploadingImageListMutation,
  [MutationType.UPDATE_SITE_SETTINGS]: updateSiteSettingsMutation,
  [MutationType.UPDATE_CONTEXT]: updateSiteContextMutation,
  [MutationType.SET_BURGER_MENU_OPEN_STATE]: setBurgerMenuOpenStateMutation,
};

export default function createMutation(): MutationTree<State> {
  return { ...mutations };
}
