function componentName(tileType: TileType): string | undefined {
  switch (tileType) {
    case 'TEXT':
      return 'TextTile';
    case 'CALL_TO_ACTION':
      return 'CTATile';
    case 'FEATURE_LIST_WITH_ICONS':
      return 'FeatureListTile';
    case 'COVER':
      return 'CoverTile';
    case 'STORE':
      return 'StoreTile';
    case 'IMAGE_TEXT':
      return 'ImageTextTile';
    case 'HEADER':
      return 'HeaderTile';
    case 'FOOTER':
      return 'FooterTile';
    case 'LOCATION':
      return 'LocationTile';
    case 'PRODUCT_BROWSER':
      return 'ProductBrowserTile';
    case 'GDPR_BANNER':
      return 'GDPRBannerTile';
    case 'FEATURED_PRODUCTS':
      return 'FeaturedProductsTile';
    case 'ROOT_CATEGORIES':
      return 'RootCategoriesTile';
    default:
      return undefined;
  }
}

function hydrateTileAlways(tileType: TileType): boolean {
  switch (tileType) {
    case 'HEADER':
    case 'COVER':
    case 'LOCATION':
    case 'GDPR_BANNER':
    case 'PRODUCT_BROWSER':
      return true;
    default:
      return false;
  }
}

function hydrateTileWhenVisible(
  layoutAppearanceEffect: AppearanceEffectType,
  tileType: TileType,
): boolean {
  if (layoutAppearanceEffect !== AppearanceEffectType.NONE) {
    return true;
  }

  return hydrateTileAlways(tileType);
}

function ssrOnlyTile(
  tileType: TileType,
): boolean {
  switch (tileType) {
    default:
      return false;
  }
}

function useTileLoadingState(
  loadedTileIdList: Ref<string[]>,
): {
  markTileAsLoaded: (tileId: string) => void;
  markTileAsUnloaded: (tileId: string) => void;
} {
  function markTileAsLoaded(value: string): void {
    const loadedTileId = loadedTileIdList.value.find((tileId) => tileId === value);
    if (loadedTileId === undefined) {
      loadedTileIdList.value.push(value);
    }
    triggerTileLoadedEvent(value);
  }

  function markTileAsUnloaded(value: string): void {
    const loadedTileId = loadedTileIdList.value.findIndex((tileId) => tileId === value);
    if (loadedTileId !== -1) {
      loadedTileIdList.value.splice(loadedTileId, 1);
    }
    triggerTileUnloadedEvent(value);
  }

  return {
    markTileAsLoaded,
    markTileAsUnloaded,
  };
}

function siteViewportStyles(
  viewportWidth?: number,
  viewportHeight?: number,
): StyleKeyValue[] {
  const style = [];
  if (viewportWidth !== 0) {
    style.push({ key: '--vw', value: viewportWidth?.toString() });
  }
  if (viewportHeight !== 0) {
    style.push({ key: '--vh', value: viewportHeight?.toString() });
  }
  return style;
}

function siteFontFamilyStyles(fontFamily?: GlobalFonts): StyleKeyValue[] {
  const style = [
    {
      key: '--global-title-font-family',
      value: fontFamilyValue(
        fontWithFallback(fontFamily?.title),
      ),
    },
    {
      key: '--global-body-font-family',
      value: fontFamilyValue(
        fontWithFallback(fontFamily?.body),
      ),
    },
  ];
  return style;
}

function siteColorStyles(color?: GlobalColors): StyleKeyValue[] {
  const style = [
    ...colorStyles('--global-title-color', color?.title),
    ...colorStyles('--global-body-color', color?.body),
    ...colorStyles('--global-background-color', color?.background),
    ...colorStyles('--global-link-color', color?.link),
    ...colorStyles('--global-button-color', color?.button),
  ];
  return style;
}

function siteTextSizeStyles(textSize?: GlobalTextSize): StyleKeyValue[] {
  const titleSize = textSize?.title !== undefined ? `${textSize.title}px` : undefined;
  const subtitleSize = textSize?.subtitle !== undefined ? `${textSize.subtitle}px` : undefined;
  const bodySize = textSize?.body !== undefined ? `${textSize.body}px` : undefined;
  const style = [
    { key: '--global-title-font-size', value: titleSize },
    { key: '--global-subtitle-font-size', value: subtitleSize },
    { key: '--global-body-font-size', value: bodySize },
  ];
  return style;
}

function siteButtonSizeStyles(buttonSize?: GlobalButtonSize): StyleKeyValue[] {
  const smallSize = buttonSize?.small !== undefined ? `${buttonSize.small}px` : undefined;
  const mediumSize = buttonSize?.medium !== undefined ? `${buttonSize.medium}px` : undefined;
  const largeSize = buttonSize?.large !== undefined ? `${buttonSize.large}px` : undefined;
  const style = [
    { key: '--global-button-size-small', value: smallSize },
    { key: '--global-button-size-medium', value: mediumSize },
    { key: '--global-button-size-large', value: largeSize },
  ];
  return style;
}

function siteLayoutStyles(layout?: GlobalLayout): StyleKeyValue[] {
  const maxWidth = layout?.maxWidth !== undefined ? `${layout.maxWidth}` : undefined;
  const margin = layout?.tileMargin !== undefined ? `${layout.tileMargin}` : undefined;

  const style = [
    { key: '--global-tile-max-width', value: maxWidth },
    { key: '--global-tile-margin', value: margin },
  ];
  return style;
}

function siteStyles(
  siteSettings?: GlobalDesign,
  viewportWidth?: number,
  viewportHeight?: number,
): StyleKeyValue[] {
  const styles = [
    ...siteViewportStyles(viewportWidth, viewportHeight),
    ...siteFontFamilyStyles(siteSettings?.fontFamily),
    ...siteColorStyles(siteSettings?.color),
    ...siteTextSizeStyles(siteSettings?.textSize),
    ...siteButtonSizeStyles(siteSettings?.buttonSize),
    ...siteLayoutStyles(siteSettings?.layout),
  ];

  return styles;
}

function useViewportDimensionSettings(): {
  viewportWidth: Ref<number>;
  viewportHeight: Ref<number>;
  subscribeOnViewportDimensionChange: () => void;
  } {
  const viewportWidth = ref(0);
  const viewportHeight = ref(0);

  function updateViewportDimensions(): void {
    if (typeof window === 'undefined') {
      return;
    }

    const isSameWidth = viewportWidth.value === window.innerWidth;
    const isTouchDevice = 'ontouchstart' in window;

    if (!isSameWidth || (isSameWidth && !isTouchDevice)) {
      viewportWidth.value = window.innerWidth;
      viewportHeight.value = window.innerHeight;
    }
  }

  function subscribeOnViewportDimensionChange(): void {
    const debounceUpdateViewportDimensions = debounce(updateViewportDimensions, 100);
    window.addEventListener('resize', debounceUpdateViewportDimensions);
    updateViewportDimensions();
  }

  return {
    viewportWidth,
    viewportHeight,
    subscribeOnViewportDimensionChange,
  };
}

function triggerResizeEvent(): void {
  const resizeEvent = new CustomEvent('resize');
  window.dispatchEvent(resizeEvent);
}

function triggerScrollEvent(): void {
  const scrollEvent = new CustomEvent('scroll');
  window.dispatchEvent(scrollEvent);
}

function isPreviewVisible(): boolean {
  return document && document.body.offsetHeight > 0;
}

function createMetaProperty(property: string, value?: string): MetaPropertyProperty {
  return {
    property,
    content: value ?? '',
    vmid: property,
  };
}

function createMetaInfo(store: Store<State>):MetaInfo {
  const effectiveSeoSettings = store.state.siteSettings?.externalContent?.effectiveSeoSettings;
  return {
    title: effectiveSeoSettings?.title,
    meta: [
      {
        name: 'description',
        content: effectiveSeoSettings?.description || '',
        vmid: 'description',
      },
      createMetaProperty('og:title', effectiveSeoSettings?.title),
      createMetaProperty('og:description', effectiveSeoSettings?.description),
      createMetaProperty('og:image', effectiveSeoSettings?.socialShareImage),
      createMetaProperty('twitter:title', effectiveSeoSettings?.title),
      createMetaProperty('twitter:description', effectiveSeoSettings?.description),
      createMetaProperty('twitter:image', effectiveSeoSettings?.socialShareImage),
    ],
  };
}

function isHeadTile(index: number): boolean {
  // верхний после хедера тайл имеет индекс 1
  return index === 1;
}

const Main = defineComponent({
  components: {
    LazyHydrate,
    FontLoader,
    NoticeWrap,
  },
  metaInfo(): MetaInfo {
    return this.metaInfo;
  },
  setup(_, { root }) {
    const state = computed(() => root.$store.state as State);
    const profile = computed(() => state.value.profile);
    const blockTileList = computed(() => root.$store.getters.visibleBlockTileList as TileData[]);
    const noticeTileList = computed(() => root.$store.getters.visibleNoticeTileList as TileData[]);
    const ssrRendered = computed(() => state.value.context.ssrRendered);

    const {
      viewportWidth,
      viewportHeight,
      subscribeOnViewportDimensionChange,
    } = useViewportDimensionSettings();

    const globalFonts = computed(() => [
      fontWithFallback(
        root.$store.state.siteSettings?.design?.fontFamily?.title,
      ),
      fontWithFallback(
        root.$store.state.siteSettings?.design?.fontFamily?.body,
      ),
    ]);

    const globalStyles = computed(() => {
      const styles = siteStyles(
        root.$store.state.siteSettings?.design,
        viewportWidth.value,
        viewportHeight.value,
      );
      return tileStyleSelector(':root', styles);
    });

    const viewportSettings = computed(() => {
      const settings = {
        width: viewportWidth.value,
        height: viewportHeight.value,
      };
      return settings;
    });

    const tileMaxWidth = computed(() => root.$store.state.siteSettings?.design.layout?.maxWidth);

    const layoutAppearanceEffect = computed(
      () => state.value.siteSettings?.design?.layout?.appearanceEffect,
    );
    const layoutAppearanceEffectClass = computed(() => getLayoutAppearanceEffectClass(
      layoutAppearanceEffect.value,
    ));

    const hasOverlay = computed(() => root.$store.getters.isBurgerMenuOpened);

    const loadedBlockTileIdList = ref<string[]>([]);
    const allBlockTilesLoaded = computed(() => {
      if (blockTileList.value.length === 0) {
        return false;
      }

      return loadedBlockTileIdList.value.length === blockTileList.value.length;
    });
    const {
      markTileAsLoaded,
      markTileAsUnloaded,
    } = useTileLoadingState(loadedBlockTileIdList);

    const metaInfo = computed(() => createMetaInfo(root.$store));

    const scrollPosition = useScrollPosition(root.$store);

    const focusedTileId = computed(() => state.value.focusedTileId);
    watch([focusedTileId, blockTileList], (newValues) => {
      if (!allBlockTilesLoaded.value || !isPreviewVisible()) {
        return;
      }

      const newFocusedTileId = newValues[0] as string | undefined;
      const newBlockTileList = newValues[1] as TileData[];
      const focusedTileExists = newBlockTileList.some((tile) => tile.id === newFocusedTileId);
      if (focusedTileExists) {
        scrollPosition.scrollToTile(newFocusedTileId, true);
      }
    });

    const trackingConsent = useTrackingConsent(root.$store);

    onMounted(() => {
      subscribeOnViewportDimensionChange();
      scrollPosition.setScrollPosition();
      initTracking(
        profile.value,
        trackingConsent.trackingConsentEnabled,
        trackingConsent.trackingAllowed,
        blockTileList.value,
        state.value.context.heapScriptUrl,
      );
      state.value.context.appJsUrls?.forEach((url) => loadScript(url));
      state.value.context.appCssUrls?.forEach((url) => loadCss(url));
    });

    onUpdated(() => {
      if (!allBlockTilesLoaded.value || !isPreviewVisible()) {
        return;
      }
      triggerResizeEvent();
      triggerScrollEvent();
    });

    return {
      profile,
      blockTileList,
      noticeTileList,
      ssrRendered,
      componentName,
      hydrateTileWhenVisible,
      ssrOnlyTile,
      globalFonts,
      globalStyles,
      viewportSettings,
      tileMaxWidth,
      layoutAppearanceEffect,
      layoutAppearanceEffectClass,
      hasOverlay,
      allBlockTilesLoaded,
      markTileAsLoaded,
      markTileAsUnloaded,
      metaInfo,
      isHeadTile,
    };
  },
});

export default Main;
