import { Ref, watch } from '@vue/composition-api';
import { loadTrackingScript } from '@/app/website/client/logic/tracking/integration-code/fb-pixel';

function startTracking(trackingId: string): void {
  const { fbq } = window;
  if (fbq) {
    fbq('init', trackingId);
    fbq('track', 'PageView');
  }
}

function changeTrackingConsent(trackingAllowed: boolean): void {
  const { fbq } = window;
  if (fbq) {
    const consent = trackingAllowed ? 'grant' : 'revoke';
    fbq('consent', consent);
  }
}

export function initTracking(
  trackingId: string | undefined,
  trackingAllowed: Ref<boolean>,
): void {
  if (trackingId == null) {
    return;
  }

  loadTrackingScript();
  watch(trackingAllowed, (newTrackingAllowedValue) => {
    changeTrackingConsent(newTrackingAllowedValue);
  }, { immediate: true });
  startTracking(trackingId);
}

export function sendSearchEvent(keyword: string): void {
  const { fbq } = window;
  if (fbq) {
    fbq('track', 'Search', {
      search_string: keyword,
    });
  }
}
