import {
  Font,
  SystemFont,
  GoogleFont,
  FontKeyLabel,
} from '@/app/shared/client/type/font';

export function fontFamilyValue(font?: Font): string | undefined {
  switch (font) {
    case SystemFont.ARIAL:
      return 'Arial';
    case SystemFont.GEORGIA:
      return 'Georgia';
    case SystemFont.HELVETICA:
      return 'Helvetica';
    case SystemFont.TIMES_NEW_ROMAN:
      return 'Times New Roman';
    case GoogleFont.ALEGREYA:
      return 'Alegreya';
    case GoogleFont.ARSENAL:
      return 'Arsenal';
    case GoogleFont.BITTER:
      return 'Bitter';
    case GoogleFont.COMFORTAA:
      return 'Comfortaa';
    case GoogleFont.CORMORANT_GARAMOND:
      return 'Cormorant Garamond';
    case GoogleFont.DIDACT_GOTHIC:
      return 'Didact Gothic';
    case GoogleFont.EXO_TWO:
      return 'Exo Two';
    case GoogleFont.FIRA_SANS:
      return 'Fira Sans';
    case GoogleFont.FIRA_SANS_EXTRA_CONDENSED:
      return 'Fira Sans Extra Condensed';
    case GoogleFont.GRANDSTANDER:
      return 'Grandstander';
    case GoogleFont.IBM_PLEX_SANS:
      return 'IBM Plex Sans';
    case GoogleFont.IBM_PLEX_SERIF:
      return 'IBM Plex Serif';
    case GoogleFont.INKNUT_ANTIQUA:
      return 'Inknut Antiqua';
    case GoogleFont.INTER:
      return 'Inter';
    case GoogleFont.KRONA_ONE:
      return 'Krona One';
    case GoogleFont.LORA:
      return 'Lora';
    case GoogleFont.MANROPE:
      return 'Manrope';
    case GoogleFont.MERRIWEATHER:
      return 'Merriweather';
    case GoogleFont.MONTSERRAT:
      return 'Montserrat';
    case GoogleFont.NOTO_SANS:
      return 'Noto Sans';
    case GoogleFont.NUNITO:
      return 'Nunito';
    case GoogleFont.OLD_STANDARD_TT:
      return 'Old Standard TT';
    case GoogleFont.OPEN_SANS:
      return 'Open Sans';
    case GoogleFont.OSWALD:
      return 'Oswald';
    case GoogleFont.PLAYFAIR_DISPLAY:
      return 'Playfair Display';
    case GoogleFont.POPPINS:
      return 'Poppins';
    case GoogleFont.PRATA:
      return 'Prata';
    case GoogleFont.PT_MONO:
      return 'PT Mono';
    case GoogleFont.PT_SANS:
      return 'PT Sans';
    case GoogleFont.PT_SERIF:
      return 'PT Serif';
    case GoogleFont.PT_SERIF_CAPTION:
      return 'PT Serif Caption';
    case GoogleFont.QUESTRIAL:
      return 'Questrial';
    case GoogleFont.RALEWAY:
      return 'Raleway';
    case GoogleFont.ROBOTO:
      return 'Roboto';
    case GoogleFont.ROBOTO_CONDENSED:
      return 'Roboto Condensed';
    case GoogleFont.ROBOTO_MONO:
      return 'Roboto Mono';
    case GoogleFont.ROBOTO_SLAB:
      return 'Roboto Slab';
    case GoogleFont.RUBIK:
      return 'Rubik';
    case GoogleFont.SORA:
      return 'Sora';
    case GoogleFont.SOURCE_SANS_PRO:
      return 'Source Sans Pro';
    case GoogleFont.SPECTRAL:
      return 'Spectral';
    case GoogleFont.UBUNTU:
      return 'Ubuntu';
    case GoogleFont.WORK_SANS:
      return 'Work Sans';
    case GoogleFont.YANONE_KAFFEESATZ:
      return 'Yanone Kaffeesatz';
    case GoogleFont.YESEVA_ONE:
      return 'Yeseva One';
    default:
      return undefined;
  }
}

function fontKeyLabel(font: Font): FontKeyLabel {
  return {
    key: font,
    label: fontFamilyValue(font),
  };
}

export function fontKeyLabelList(): FontKeyLabel[] {
  return [
    ...Object.values(SystemFont).map((font: string) => fontKeyLabel(font as SystemFont)),
    ...Object.values(GoogleFont).map((font: string) => fontKeyLabel(font as GoogleFont)),
  ];
}

function isFontExists(font?: Font): boolean {
  return Object.values(SystemFont).includes(font as SystemFont)
    || Object.values(GoogleFont).includes(font as GoogleFont);
}

export function getFontIfExists(font?: Font): SystemFont | GoogleFont | undefined {
  return isFontExists(font)
    ? font
    : undefined;
}

export function fontWithFallback(font?: Font): SystemFont | GoogleFont | undefined {
  if (isFontExists(font)) {
    return font;
  }
  return GoogleFont.MONTSERRAT;
}
