import { State } from '@/app/website/client/store/state';
import { MutationPayload } from 'vuex';
import { MutationType } from '@/app/website/client/store/mutation/type';

interface SetBurgerMenuOpenStateMutationPayload {
  burgerMenuOpened: boolean;
}

export interface SetBurgerMenuOpenStateMutation extends MutationPayload {
  type: MutationType.SET_BURGER_MENU_OPEN_STATE;
  payload: SetBurgerMenuOpenStateMutationPayload;
}

export default function setBurgerMenuOpenStateMutation(
  oldState: State,
  data: SetBurgerMenuOpenStateMutation,
): State {
  const newState = oldState;
  newState.burgerMenuOpened = data.payload.burgerMenuOpened;
  return newState;
}
