import { Store } from 'vuex';
import { State } from '@/app/website/client/store/state';
import { MutationType } from '@/app/website/client/store/mutation/type';
import { UpdateTileListMutation } from '@/app/website/client/store/mutation/update-tile-list';
import {
  PreviewApiEventPayload,
  PreviewApiEventType, SetFocusedTileIdPayload,
  UpdatePreviewContextPayload,
  UpdatePreviewProfile,
  UpdatePreviewSiteSettings,
  UpdatePreviewTileListPayload, UpdatePreviewUploadingImageList,
} from '@/app/shared/client/type/preview-api';
import { SetFocusedTileIdMutation } from '@/app/website/client/store/mutation/set-focused-tile-id';
import { SetUploadingImageListMutation } from '@/app/website/client/store/mutation/set-uploading-image-list';
import { useScrollPosition } from '@/app/website/client/logic/scroll-position';
import { SetProfileMutation } from '@/app/website/client/store/mutation/set-profile';
import { omitObjectUndefinedFields } from '@/app/shared/client/helper/base';

function setFocusedTileId(store: Store<State>, data: SetFocusedTileIdPayload): void {
  store.commit<SetFocusedTileIdMutation>({
    type: MutationType.SET_FOCUSED_TILE_ID,
    payload: { focusedTileId: data.payload.focusedTileId },
  });
}

function updatePreviewTileList(
  store: Store<State>,
  data: UpdatePreviewTileListPayload,
): void {
  const { tileList } = data.payload;
  store.commit<UpdateTileListMutation>({
    type: MutationType.UPDATE_TILE_LIST,
    payload: { tileList: omitObjectUndefinedFields(tileList) },
  });
}

function updatePreviewSiteSettings(
  store: Store<State>,
  data: UpdatePreviewSiteSettings,
): void {
  store.commit(MutationType.UPDATE_SITE_SETTINGS, data.payload.siteSettings);
}

function updatePreviewContext(
  store: Store<State>,
  data: UpdatePreviewContextPayload,
): void {
  const context = {
    ...store.state.context,
    bucketUrls: data.payload.bucketUrls,
    translations: data.payload.translations,
  };
  store.commit(MutationType.UPDATE_CONTEXT, context);
}

function updatePreviewProfile(
  store: Store<State>,
  data: UpdatePreviewProfile,
): void {
  store.commit<SetProfileMutation>({
    type: MutationType.SET_PROFILE,
    payload: {
      profile: data.payload.profile,
    },
  });
}

function updatePreviewUploadingImageList(
  store: Store<State>,
  data: UpdatePreviewUploadingImageList,
): void {
  store.commit<SetUploadingImageListMutation>({
    type: MutationType.SET_UPLOADING_IMAGE_LIST,
    payload: {
      imageListInUploadingState: data.payload.imageListInUploadingState,
    },
  });
}

function saveMobilePreviewPosition(store: Store<State>): void {
  const scrollPosition = useScrollPosition(store);
  scrollPosition.saveScrollPositionForRecovery();
}

function recoveryMobilePreviewPosition(store: Store<State>): void {
  const scrollPosition = useScrollPosition(store);
  scrollPosition.recoverScrollPositionIfNeeded();
  // мобильная превьюшка раскукожилась — принудительно обработаем событие resize,
  // чтобы все тайлы пересчитали своё расположение
  const resizeEvent = new CustomEvent('resize');
  window.dispatchEvent(resizeEvent);
}

type ReceiveMessageFunction = (event: MessageEvent) => void;
function createReceiveMessageFunction(store: Store<State>): ReceiveMessageFunction {
  return function receiveMessage(event: MessageEvent): void {
    const messageData = event.data as PreviewApiEventPayload;
    switch (messageData.type) {
      case PreviewApiEventType.UPDATE_PREVIEW_TILE_LIST:
        updatePreviewTileList(store, messageData as UpdatePreviewTileListPayload);
        break;
      case PreviewApiEventType.UPDATE_PREVIEW_SITE_SETTINGS:
        updatePreviewSiteSettings(store, messageData as UpdatePreviewSiteSettings);
        break;
      case PreviewApiEventType.UPDATE_PREVIEW_CONTEXT:
        updatePreviewContext(store, messageData as UpdatePreviewContextPayload);
        break;
      case PreviewApiEventType.UPDATE_PREVIEW_PROFILE:
        updatePreviewProfile(store, messageData as UpdatePreviewProfile);
        break;
      case PreviewApiEventType.UPDATE_PREVIEW_UPLOADING_IMAGE_LIST:
        updatePreviewUploadingImageList(store, messageData as UpdatePreviewUploadingImageList);
        break;
      case PreviewApiEventType.SAVE_MOBILE_PREVIEW_POSITION:
        saveMobilePreviewPosition(store);
        break;
      case PreviewApiEventType.RECOVERY_MOBILE_PREVIEW_POSITION:
        recoveryMobilePreviewPosition(store);
        break;
      case PreviewApiEventType.SET_FOCUSED_TILE_ID:
        setFocusedTileId(store, messageData as SetFocusedTileIdPayload);
        break;
      default:
        break;
    }
  };
}

export default function initApi(store: Store<State>): void {
  window.addEventListener('message', createReceiveMessageFunction(store), false);
}
