import { useSessionStorage } from '@vueuse/core';
import { isNullOrEmpty } from '@/app/shared/client/helper/base';
import { Store } from 'vuex';
import { State } from '@/app/website/client/store/state';
import { MutationType } from '@/app/website/client/store/mutation/type';
import { nextTick } from '@vue/composition-api';
import { SetBurgerMenuOpenStateMutation } from '@/app/website/client/store/mutation/set-burger-menu-open-state';
import { Page } from '@/app/website/client/type/page';

const SCROLL_POSITION_FOR_RECOVERY_KEY = 'scrollPositionForRecovery';
const TILE_TO_SCROLL_KEY = 'tileToScroll';

interface ScrollPositionRecoveryInfo {
  scrollY: number;
  pathname: string;
}

const scrollPosition = useSessionStorage<string | undefined>(
  SCROLL_POSITION_FOR_RECOVERY_KEY,
  undefined,
);

const tileToScroll = useSessionStorage<string | undefined>(
  TILE_TO_SCROLL_KEY,
  undefined,
);

function parseScrollPositionRecoveryInfo(json: string): ScrollPositionRecoveryInfo | undefined {
  try {
    const obj = JSON.parse(json);
    if (typeof obj.scrollY === 'number' && typeof obj.pathname === 'string') {
      return obj as ScrollPositionRecoveryInfo;
    }
    return undefined;
  } catch (e) {
    return undefined;
  }
}

function saveScrollPositionForRecovery(): void {
  if (typeof window === 'undefined') {
    return;
  }

  const scrollPositionRecoveryInfo: ScrollPositionRecoveryInfo = {
    scrollY: window.scrollY,
    pathname: window.location.pathname,
  };
  scrollPosition.value = JSON.stringify(scrollPositionRecoveryInfo);
}

function saveTileToScroll(tileId: string): void {
  tileToScroll.value = tileId;
}

interface ScrollPosition {
  scrollToTile: (tileId: string | undefined, force: boolean) => void;
  saveScrollPositionForRecovery: () => void,
  recoverScrollPositionIfNeeded: () => void;
  setScrollPosition: () => void;
}
export function useScrollPosition(
  store: Store<State>,
): ScrollPosition {
  function scrollToTile(
    tileId: string | undefined,
    force: boolean,
  ): void {
    if (typeof document === 'undefined') {
      return;
    }

    if (isNullOrEmpty(tileId) || tileId.startsWith('<#TILE_ID')) {
      return;
    }

    store.commit<SetBurgerMenuOpenStateMutation>({
      type: MutationType.SET_BURGER_MENU_OPEN_STATE,
      payload: {
        burgerMenuOpened: false,
      },
    });
    nextTick().then(() => {
      const tileElement = document.getElementById(`tile-${tileId}`);
      if (tileElement === null) {
        if (store.state.context.page === Page.MAIN) {
          return;
        }

        // выбран тайл для скролла, однако на текущей странице его нет.
        // попытаем счастье на главной.
        saveTileToScroll(tileId);
        window.location.href = window.location.origin;
        return;
      }

      const overTop = tileElement.offsetTop > window.scrollY + window.innerHeight;
      const overBottom = tileElement.offsetTop + tileElement.offsetHeight < window.scrollY;
      if (overTop || overBottom || force) {
        tileElement.scrollIntoView({ behavior: 'smooth' });
      }
    });
  }

  function recoverScrollPositionIfNeeded(): void {
    if (typeof document === 'undefined') {
      return;
    }

    const scrollPositionForRecoveryRaw = scrollPosition.value;
    if (scrollPositionForRecoveryRaw === undefined) {
      return;
    }

    const recoveryInfo = parseScrollPositionRecoveryInfo(scrollPositionForRecoveryRaw);
    if (recoveryInfo === undefined) {
      return;
    }

    if (window.location.pathname === recoveryInfo.pathname) {
      window.scrollTo(0, recoveryInfo.scrollY);
      scrollPosition.value = undefined;
    }
  }

  function setScrollPosition(): void {
    const tileId = tileToScroll.value;
    if (tileId) {
      scrollToTile(tileId, false);
      tileToScroll.value = undefined;
    } else {
      recoverScrollPositionIfNeeded();
      window.addEventListener('pageshow', () => {
        // для восстановления позиции скролла в браузерах с bfcache
        recoverScrollPositionIfNeeded();
      });
    }
  }

  return {
    scrollToTile,
    saveScrollPositionForRecovery,
    recoverScrollPositionIfNeeded,
    setScrollPosition,
  };
}

export default {};
