import { Color } from '@/app/shared/client/type/color';
import { GlobalColorsString } from '@/app/shared/client/type/global-string';

//------------------------------------------------------------------------------
// General Design settings
//------------------------------------------------------------------------------

export enum BackgroundType {
  SOLID = 'solid',
  GRADIENT = 'gradient'
}

export enum AppearanceEffectType {
  NONE = 'none',
  FADE_IN = 'fade-in',
  FADE_IN_UP = 'fade-in-up'
}

interface SolidSettings {
  color?: Color;
}

interface GradientSettings {
  fromColor?: Color;
  toColor?: Color;
}

interface SolidDefaults {
  color?: Color | GlobalColorsString;
}

interface GradientDefaults {
  fromColor?: Color | GlobalColorsString;
  toColor?: Color | GlobalColorsString;
}

interface BackgroundSettings {
  type?: BackgroundType;
  solid?: SolidSettings;
  gradient?: GradientSettings;
}

interface BackgroundDefaults {
  type?: BackgroundType;
  solid?: SolidDefaults;
  gradient?: GradientDefaults;
}

export interface GeneralDesignSettings {
  background?: BackgroundSettings;
}

export interface GeneralDesignDefaults {
  background?: BackgroundDefaults;
}
