import { Ref, watch } from '@vue/composition-api';
import { injectScript } from '@/app/website/client/helper';
import { initTrackingFunction } from '@/app/website/client/logic/tracking/integration-code/google-global-tag';

enum ConsentMode {
  GRANTED = 'granted',
  DENIED = 'denied',
}

enum ConsentArg {
  DEFAULT = 'default',
  UPDATE = 'update',
}

function isGoogleGlobalTagInjected() : boolean {
  const { gtag } = window;
  return typeof gtag === 'function';
}

function loadTrackingScript(trackingId: string) : void {
  injectScript(`https://www.googletagmanager.com/gtag/js?id=${trackingId}`);
}

function setConsentMode(consentArg: ConsentArg, consentMode: ConsentMode) : void {
  const { gtag } = window;
  gtag('consent', consentArg, {
    ad_storage: consentMode,
    analytics_storage: consentMode,
  });
  if (consentMode === ConsentMode.DENIED) {
    gtag('set', 'ads_data_redaction', true);
  }
}

function trackingFlagToConsentMode(trackingAllowed: boolean) : ConsentMode {
  return trackingAllowed ? ConsentMode.GRANTED : ConsentMode.DENIED;
}

function changeConsentMode(consentArg: ConsentArg, trackingAllowed: boolean) : void {
  const consentMode = trackingFlagToConsentMode(trackingAllowed);
  setConsentMode(consentArg, consentMode);
}

export function startTracking(trackingId: string): void {
  const { gtag } = window;
  gtag('config', trackingId);
}

export function configureTracking(trackingId: string, trackingAllowed: Ref<boolean>): void {
  if (isGoogleGlobalTagInjected()) {
    return;
  }

  initTrackingFunction();

  changeConsentMode(ConsentArg.DEFAULT, trackingAllowed?.value);

  watch(trackingAllowed, (newTrackingAllowedValue) => {
    changeConsentMode(ConsentArg.UPDATE, newTrackingAllowedValue);
  });

  loadTrackingScript(trackingId);
}

export default {};
