










import {
  computed, defineComponent, PropType, toRefs,
} from '@vue/composition-api';
import { Font, GoogleFont } from '@/app/shared/client/type/font';
import Vue from 'vue';
import { hydrateNever } from 'vue-lazy-hydration';

function fontComponentName(font: Font): string | undefined {
  if (!Object.values(GoogleFont).includes(font as GoogleFont)) {
    return undefined;
  }

  switch (font) {
    case GoogleFont.ALEGREYA:
      return 'AlegreyaFont';
    case GoogleFont.ARSENAL:
      return 'ArsenalFont';
    case GoogleFont.BITTER:
      return 'BitterFont';
    case GoogleFont.COMFORTAA:
      return 'ComfortaaFont';
    case GoogleFont.CORMORANT_GARAMOND:
      return 'CormorantGaramondFont';
    case GoogleFont.DIDACT_GOTHIC:
      return 'DidactGothicFont';
    case GoogleFont.EXO_TWO:
      return 'ExoTwoFont';
    case GoogleFont.FIRA_SANS:
      return 'FiraSansFont';
    case GoogleFont.FIRA_SANS_EXTRA_CONDENSED:
      return 'FiraSansExtraCondensedFont';
    case GoogleFont.GRANDSTANDER:
      return 'GrandstanderFont';
    case GoogleFont.IBM_PLEX_SANS:
      return 'IBMPlexSansFont';
    case GoogleFont.IBM_PLEX_SERIF:
      return 'IBMPlexSerifFont';
    case GoogleFont.INKNUT_ANTIQUA:
      return 'InknutAntiquaFont';
    case GoogleFont.INTER:
      return 'InterFont';
    case GoogleFont.KRONA_ONE:
      return 'KronaOneFont';
    case GoogleFont.LORA:
      return 'LoraFont';
    case GoogleFont.MANROPE:
      return 'ManropeFont';
    case GoogleFont.MERRIWEATHER:
      return 'MerriweatherFont';
    case GoogleFont.MONTSERRAT:
      return 'MontserratFont';
    case GoogleFont.NOTO_SANS:
      return 'NotoSansFont';
    case GoogleFont.NUNITO:
      return 'NunitoFont';
    case GoogleFont.OLD_STANDARD_TT:
      return 'OldStandardTTFont';
    case GoogleFont.OPEN_SANS:
      return 'OpenSansFont';
    case GoogleFont.OSWALD:
      return 'OswaldFont';
    case GoogleFont.PLAYFAIR_DISPLAY:
      return 'PlayfairDisplayFont';
    case GoogleFont.POPPINS:
      return 'PoppinsFont';
    case GoogleFont.PRATA:
      return 'PrataFont';
    case GoogleFont.PT_MONO:
      return 'PTMonoFont';
    case GoogleFont.PT_SANS:
      return 'PTSansFont';
    case GoogleFont.PT_SERIF:
      return 'PTSerifFont';
    case GoogleFont.PT_SERIF_CAPTION:
      return 'PTSerifCaptionFont';
    case GoogleFont.QUESTRIAL:
      return 'QuestrialFont';
    case GoogleFont.RALEWAY:
      return 'RalewayFont';
    case GoogleFont.ROBOTO:
      return 'RobotoFont';
    case GoogleFont.ROBOTO_CONDENSED:
      return 'RobotoCondensedFont';
    case GoogleFont.ROBOTO_MONO:
      return 'RobotoMonoFont';
    case GoogleFont.ROBOTO_SLAB:
      return 'RobotoSlabFont';
    case GoogleFont.RUBIK:
      return 'RubikFont';
    case GoogleFont.SORA:
      return 'SoraFont';
    case GoogleFont.SOURCE_SANS_PRO:
      return 'SourceSansProFont';
    case GoogleFont.SPECTRAL:
      return 'SpectralFont';
    case GoogleFont.UBUNTU:
      return 'UbuntuFont';
    case GoogleFont.WORK_SANS:
      return 'WorkSansFont';
    case GoogleFont.YANONE_KAFFEESATZ:
      return 'YanoneKaffeesatzFont';
    case GoogleFont.YESEVA_ONE:
      return 'YesevaOneFont';
    default:
      return undefined;
  }
}

export function registerAllFontComponents(): void {
  Object.values(GoogleFont).forEach((font) => {
    const componentName = fontComponentName(font);
    if (componentName === undefined) {
      return;
    }

    Vue.component(
      componentName,
      hydrateNever(
        () => import(
          /* webpackChunkName: "font" */
          (`./font/${componentName}.vue`)
        ),
      ),
    );
  });
}

const FontLoader = defineComponent({
  props: {
    fonts: {
      required: true,
      type: Array as PropType<Font[]>,
    },
  },
  setup(props) {
    const { fonts } = toRefs(props);
    const fontsForLoading = computed(() => fonts.value.map(fontComponentName));

    return {
      fontsForLoading,
    };
  },
});

export default FontLoader;
