import { State } from '@/app/website/client/store/state';
import { WebsiteRuntimeContext } from '@/app/website/client/type/website-runtime-context';

export default function updateSiteContextMutation(
  oldState: State,
  data: WebsiteRuntimeContext,
): State {
  const newState = oldState;
  newState.context = data;
  return newState;
}
