import { Ref, watch } from '@vue/composition-api';
import { loadTrackingScript } from '@/app/website/client/logic/tracking/integration-code/tiktok-pixel';

function startTracking(): void {
  const { ttq } = window;
  if (ttq) {
    ttq.track('Browse');
  }
}

function configureAndStartTracking(
  code: string,
  trackingAllowed: boolean,
): void {
  if (!trackingAllowed) {
    return;
  }

  loadTrackingScript(code);
  startTracking();
}

export function initTracking(
  code: string | undefined,
  trackingAllowed: Ref<boolean>,
): void {
  if (code == null || !code.trim()) {
    return;
  }

  watch(trackingAllowed, (newTrackingAllowedValue) => {
    configureAndStartTracking(code, newTrackingAllowedValue);
  }, { immediate: true });
}

export default {};
