import { GetterTree } from 'vuex';
import { State } from '@/app/website/client/store/state';
import { TileData } from '@/app/shared/client/type/tile';

function isPreviewMode(state: State): boolean {
  return state.context?.previewMode ?? false;
}

function isBurgerMenuOpened(state: State): boolean {
  return state.burgerMenuOpened ?? false;
}

function visibleBlockTileList(state: State): TileData[] {
  return state.tileList
    .filter((tile) => tile.visibility)
    .filter((tile) => tile.role === 'BLOCK');
}

function visibleNoticeTileList(state: State): TileData[] {
  return state.tileList
    .filter((tile) => tile.visibility)
    .filter((tile) => tile.role === 'NOTICE');
}

export default function createGetters(): GetterTree<State, State> {
  return {
    isPreviewMode,
    isBurgerMenuOpened,
    visibleBlockTileList,
    visibleNoticeTileList,
  };
}
