import { Ref, watch } from '@vue/composition-api';
import { HeapData } from '@/app/website/client/type/heap-api';
import { loadTrackingScript } from '@/app/website/client/logic/tracking/integration-code/heap';

function changeTrackingConsent(trackingAllowed: boolean): void {
  const eca = window.eca;
  if (!eca) {
    return;
  }
  if (trackingAllowed) {
    eca('config', 'allowCookies', trackingAllowed);
  } else {
    eca('config', 'revokeCookies');
  }
}

function getHeapData(storeId?: number): HeapData {
  return {
    eventName: 'PAGE_VIEW',
    entityType: 'PAGE',
    storeId,
    siteUrl: window.location.origin,
    fullUrl: window.location.href,
  };
}

function sendPageView(storeId?: number): void {
  const eca = window.eca;
  if (eca) {
    eca('send', getHeapData(storeId));
  }
}

export function initHeapTracking(
  heapEnabled: boolean | undefined,
  heapScriptUrl: string | undefined,
  storeId: number | undefined,
  trackingAllowed: Ref<boolean>,
): void {
  if (!heapEnabled || heapScriptUrl == null) {
    return;
  }

  loadTrackingScript(heapScriptUrl);
  sendPageView(storeId);

  watch(trackingAllowed, (newTrackingAllowedValue) => {
    changeTrackingConsent(newTrackingAllowedValue);
  }, { immediate: true });
}

export default {};
