import Vue from 'vue';

function initBundleChunkBaseUrl(): void {
  let baseUrl = window.previewApi.bundleChunkBaseUrl;
  if (baseUrl) {
    if (!baseUrl.endsWith('/')) {
      baseUrl += '/';
    }
    // eslint-disable-next-line camelcase
    __webpack_public_path__ = baseUrl;
  }
}

function initVueDevelopmentDebug(): void {
  Vue.config.silent = false;
  Vue.config.devtools = true;
  Vue.config.productionTip = false;
}

function initVueProductionDebug(): void {
  const vueDebug = Boolean(window.localStorage.getItem('ec-instantsite-debug'));
  Vue.config.silent = !vueDebug;
  Vue.config.devtools = vueDebug;
  Vue.config.productionTip = false;
}

function initVueDebug(): void {
  if (process.env.NODE_ENV === 'development') {
    initVueDevelopmentDebug();
    return;
  }

  initVueProductionDebug();
}

export default function preconfigure(): void {
  initBundleChunkBaseUrl();
  initVueDebug();
}
