import Vue from 'vue';
import CompositionApi from '@vue/composition-api';
import { VNode } from 'vue/types/vnode';
import App from '@/app/website/client/view/Main.vue';
import createStore from '@/app/website/client/store';
import { PreviewApp } from '@/app/preview/client/type/preview-app';
import { WebsiteRuntimeContext } from '@/app/website/client/type/website-runtime-context';
import { Store } from 'vuex';
import { State } from '@/app/website/client/store/state';
import { InitContextMutation } from '@/app/website/client/store/mutation/init-context';
import { MutationType } from '@/app/website/client/store/mutation/type';

Vue.use(CompositionApi);

function initAppContext(store: Store<State>, context: WebsiteRuntimeContext): void {
  const payload = { context };
  store.commit<InitContextMutation>({ type: MutationType.INIT_CONTEXT, payload });
}

function initApp(store: Store<State>): Vue {
  const app = new Vue({
    store,
    render: (h): VNode => h(App),
  });

  return app;
}

export default function createApp(webpSupported: boolean): PreviewApp {
  const { store } = createStore();
  const context = {
    ...store.state.context,
    previewMode: true,
    webpSupported,
  };

  initAppContext(store, context);

  const app = initApp(store);

  return { app, store };
}
