import { Ref, watch } from '@vue/composition-api';
import { loadTrackingScript } from '@/app/website/client/logic/tracking/integration-code/snap-pixel';

function startTracking(trackingId: string): void {
  const { snaptr } = window;
  if (snaptr) {
    snaptr('init', trackingId);
    snaptr('track', 'PAGE_VIEW');
  }
}

function configureAndStartTracking(
  trackingId: string,
  trackingAllowed: boolean,
): void {
  if (!trackingAllowed) {
    return;
  }

  loadTrackingScript();
  startTracking(trackingId);
}

export function initTracking(
  trackingId: string | undefined,
  trackingAllowed: Ref<boolean>,
): void {
  if (trackingId == null) {
    return;
  }

  watch(trackingAllowed, (newTrackingAllowedValue) => {
    configureAndStartTracking(trackingId, newTrackingAllowedValue);
  }, { immediate: true });
}

export function sendSearchEvent(keyword: string): void {
  const { snaptr } = window;
  if (snaptr) {
    snaptr('track', 'SEARCH', {
      search_string: keyword,
    });
  }
}
