export function injectScript(url: string, async = true): HTMLScriptElement {
  const head = document.head || document.getElementsByTagName('head')[0];
  const script = document.createElement('script');

  script.async = async;
  script.src = url;

  head.appendChild(script);

  return script;
}

export function loadScript(url: string, async = true): Promise<Event> {
  return new Promise((resolve, reject) => {
    const script = injectScript(url, async);

    script.onload = resolve;
    script.onerror = reject;
  });
}

export function injectCss(url: string): HTMLLinkElement {
  const head = document.head || document.getElementsByTagName('head')[0];
  const link = document.createElement('link');

  link.type = 'text/css';
  link.rel = 'stylesheet';
  link.href = url;

  head.appendChild(link);
  return link;
}

export function loadCss(url: string): Promise<Event> {
  return new Promise((resolve, reject) => {
    const link = injectCss(url);

    link.onload = resolve;
    link.onerror = reject;
  });
}

const EU_COUNTRY_CODES = [
  'AT', 'BE', 'BG', 'CY', 'CZ', 'DK', 'DE', 'EE', 'ES', 'IC', 'FI', 'FR', 'GR', 'HR', 'HU', 'IE',
  'IM', 'IT', 'LV', 'LT', 'LU', 'MT', 'MC', 'NL', 'PL', 'PT', 'RO', 'SE', 'SK', 'SI',
];
export function isEU(countryCode: string | undefined): boolean {
  return EU_COUNTRY_CODES.some((euCountryCode) => euCountryCode === countryCode);
}

export function isEUorGB(countryCode: string | undefined): boolean {
  return isEU(countryCode) || countryCode === 'GB';
}

export function getLinkTarget(previewMode: boolean): string {
  if (previewMode) {
    return '_blank';
  }
  return '_self';
}

export default {};
