import { MutationPayload } from 'vuex';
import { State } from '@/app/website/client/store/state';
import { MutationType } from '@/app/website/client/store/mutation/type';
import { TileData } from '@/app/shared/client/type/tile';

interface UpdateTileListMutationPayload {
  tileList: TileData[];
}

export interface UpdateTileListMutation extends MutationPayload {
  type: MutationType.UPDATE_TILE_LIST;
  payload: UpdateTileListMutationPayload;
}

export default function updateTileListMutation(
  oldState: State,
  data: UpdateTileListMutation,
): State {
  const newState = oldState;
  newState.tileList = data.payload.tileList;
  return newState;
}
