import { Color } from '@/app/shared/client/type/color';
import {
  BackgroundType,
  GeneralDesignSettings,
} from '@/app/shared/client/type/settings/general-settings';
import { TextSettings } from '@/app/shared/client/type/settings/text-settings';
import { fontFamilyValue } from '@/app/shared/client/logic/font';
import { ImageSettings, OverlayType } from '@/app/shared/client/type/settings/image-settings';
import { ActionLinkSettings } from '@/app/shared/client/type/settings/action-link-settings';
import { IconSettings } from '@/app/shared/client/type/settings/icon-settings';
import { LogoSettings } from '@/app/shared/client/type/settings/logo-settings';
import { ImageData } from '@/app/shared/client/type/image';

export interface StyleKeyValue {
  key: string;
  value?: string;
}

export function colorStyles(
  colorPropertyName: string,
  color?: Color,
): StyleKeyValue[] {
  const styles: StyleKeyValue[] = [];
  if (color?.hsl === undefined) {
    return styles;
  }

  const colorHue = color.hsl.h;
  const colorSaturation = color.hsl.s;
  const colorLightness = color.hsl.l;
  const colorAlpha = color.hsl.a;

  styles.push({
    key: `${colorPropertyName}-h`,
    value: `${colorHue}`,
  });
  styles.push({
    key: `${colorPropertyName}-s`,
    value: `${colorSaturation * 100}%`,
  });
  styles.push({
    key: `${colorPropertyName}-l`,
    value: `${colorLightness * 100}%`,
  });
  styles.push({
    key: `${colorPropertyName}-a`,
    value: `${colorAlpha}`,
  });

  return styles;
}

export function tileStyleSelector(id: string, styles: StyleKeyValue[]): string {
  const styleVariableList = styles
    .filter(({ value }) => value !== undefined)
    .filter(({ value }) => !value?.startsWith('global.'))
    .map(({ key, value }) => `${key}: ${value};`)
    .sort()
    .join('\n');

  return `${id} {
    ${styleVariableList}
  }`;
}

export function calculateFontWeight(bold?: boolean): number {
  if (bold) {
    return 700;
  }

  return 400;
}

export function calculateFontStyle(italic?: boolean): string {
  if (italic) {
    return 'italic';
  }

  return 'normal';
}

export function generalStyles(
  general?: GeneralDesignSettings,
): StyleKeyValue[] {
  let styles: StyleKeyValue[] = [];
  switch (general?.background?.type) {
    case BackgroundType.GRADIENT:
      styles = [
        ...styles,
        ...colorStyles(
          '--background-gradient-color-from',
          general?.background?.gradient?.fromColor,
        ),
        ...colorStyles(
          '--background-gradient-color-to',
          general?.background?.gradient?.toColor,
        ),
        {
          key: '--background-solid-color-a',
          value: '0',
        },
      ];
      break;
    case BackgroundType.SOLID:
      styles = [
        ...styles,
        ...colorStyles(
          '--background-solid-color',
          general?.background?.solid?.color,
        ),
        {
          key: '--background-gradient-color-from-a',
          value: '0',
        },
        {
          key: '--background-gradient-color-to-a',
          value: '0',
        },
      ];
      break;
    default:
      break;
  }
  return styles;
}

export function textStyles(
  variableName: string,
  textSettings?: TextSettings,
): StyleKeyValue[] {
  let styles: StyleKeyValue[] = [];
  const fontSize = textSettings?.size;
  const fontFamily = textSettings?.font;
  const fontWeight = calculateFontWeight(textSettings?.bold);
  const fontStyle = calculateFontStyle(textSettings?.italic);

  styles.push({ key: `--${variableName}-font-size`, value: `${fontSize}px` });
  styles.push({ key: `--${variableName}-font-family`, value: fontFamilyValue(fontFamily) });
  styles.push({ key: `--${variableName}-font-weight`, value: fontWeight.toString() });
  styles.push({ key: `--${variableName}-font-style`, value: fontStyle });
  styles = [
    ...styles,
    ...colorStyles(`--${variableName}-text-color`, textSettings?.color),
  ];
  return styles;
}

export function imageStyles(
  variableName: string,
  image?: ImageData,
): StyleKeyValue[] {
  const width = image?.set?.['default-1200x1200']?.width ?? 0;
  const height = image?.set?.['default-1200x1200']?.height ?? 0;
  if (width && height) {
    return [{
      key: `--${variableName}-aspect-ratio`,
      value: `${(height / width) * 100}%`,
    }];
  }
  return [];
}

export function imageOverlayStyles(
  variableName: string,
  imageSettings?: ImageSettings,
): StyleKeyValue[] {
  let styles: StyleKeyValue[] = [];
  const overlay = imageSettings?.overlay;
  switch (overlay?.type) {
    case OverlayType.GRADIENT:
      styles = [
        ...styles,
        ...colorStyles(`--${variableName}-overlay-gradient-color-from`, overlay?.gradient?.fromColor)];
      styles = [
        ...styles,
        ...colorStyles(`--${variableName}-overlay-gradient-color-to`, overlay?.gradient?.toColor)];
      break;
    case OverlayType.SOLID:
      styles = [
        ...styles,
        ...colorStyles(`--${variableName}-overlay-solid-color`, overlay?.solid?.color)];
      break;
    default:
      break;
  }
  return styles;
}

export function actionLinkStyles(
  variableName: string,
  actionLinkSettings?: ActionLinkSettings,
): StyleKeyValue[] {
  let styles: StyleKeyValue[] = [];
  const fontFamily = actionLinkSettings?.font;
  styles.push({ key: `--${variableName}-font-family`, value: fontFamilyValue(fontFamily) });
  if (actionLinkSettings?.color !== undefined) {
    styles = [
      ...styles,
      ...colorStyles(`--${variableName}-color`, actionLinkSettings.color)];
  }

  return styles;
}

export function iconStyles(
  variableName: string,
  iconSettings?: IconSettings,
): StyleKeyValue[] {
  let styles: StyleKeyValue[] = [];
  styles = [
    ...colorStyles(`--${variableName}-icon-color`, iconSettings?.color),
  ];
  return styles;
}

export function logoStyles(
  variableName: string,
  logoSettings?: LogoSettings,
): StyleKeyValue[] {
  let styles: StyleKeyValue[] = textStyles(variableName, logoSettings as TextSettings);
  styles.push({ key: `--${variableName}-letter-spacing`, value: `${logoSettings?.spacing}px` });

  if (logoSettings?.frame?.visible) {
    const frameWidth = logoSettings?.frame?.width ?? 0;
    styles.push({ key: `--${variableName}-frame-width`, value: `${frameWidth}px` });
    styles = [
      ...styles,
      ...colorStyles(`--${variableName}-frame-color`, logoSettings?.frame?.color),
    ];
  }
  return styles;
}
