import { Ref } from '@vue/composition-api';
import { configureTracking, startTracking } from '@/app/website/client/logic/tracking/google-global-tag';

const GOOGLE_ANALYTICS4_ID_REGEXP = /^([Gg]-[0-9a-zA-Z]+)$/;

function isGoogleAnalytics4Id(trackingId: string) : boolean {
  return GOOGLE_ANALYTICS4_ID_REGEXP.test(trackingId);
}

export function initTracking(
  trackingId: string | undefined,
  trackingAllowed: Ref<boolean>,
): void {
  if (trackingId == null || !isGoogleAnalytics4Id(trackingId)) {
    return;
  }

  configureTracking(trackingId, trackingAllowed);
  startTracking(trackingId);
}

export function sendSearchEvent(keyword: string): void {
  const { gtag } = window;
  if (gtag) {
    gtag('event', 'search', {
      search_term: keyword,
    });
  }
}

export default {};
