import { Ref } from '@vue/composition-api';
import { initTracking as initGoogleUniversalAnalyticsTracking } from '@/app/website/client/logic/tracking/google-universal-analytics';
import { initTracking as initGoogleAdsTracking } from '@/app/website/client/logic/tracking/google-ads';
import {
  initTracking as initGoogleAnalytics4Tracking,
  sendSearchEvent as sendGoogleAnalytics4SearchEvent,
} from '@/app/website/client/logic/tracking/google-analytics4';
import {
  initTracking as initFBPixelTracking,
  sendSearchEvent as sendFBPixelTrackingSearchEvent,
} from '@/app/website/client/logic/tracking/fb-pixel';
import {
  initTracking as initSnapPixelTracking,
  sendSearchEvent as sendSnapPixelTrackingSearchEvent,
} from '@/app/website/client/logic/tracking/snap-pixel';
import {
  initTracking as initPinterestTagTracking,
  sendSearchEvent as sendPinterestTagTrackingSearchEvent,
} from '@/app/website/client/logic/tracking/pinterest-tag';
import { TileData } from '@/app/shared/client/type/tile';
import { Profile } from '@/app/shared/client/type/profile';
import { initHeapTracking } from '@/app/website/client/logic/tracking/heap';
import { initTracking as initTikTokPixelTracking } from '@/app/website/client/logic/tracking/tiktok-pixel';

function isPageWithProductBrowser(blockTileList: TileData[]): boolean {
  return blockTileList.some((tile) => tile.type === 'PRODUCT_BROWSER');
}

export function initTracking(
  profile: Profile,
  trackingConsentEnabled: Ref<boolean>,
  trackingAllowed: Ref<boolean>,
  blockTileList: TileData[],
  heapScriptUrl: string | undefined,
): void {
  const tracking = profile.tracking;
  if (tracking == null) {
    return;
  }

  if (isPageWithProductBrowser(blockTileList)) {
    return;
  }

  initHeapTracking(tracking.heapEnabled, heapScriptUrl, profile.siteId, trackingAllowed);
  initGoogleUniversalAnalyticsTracking(
    tracking.googleUniversalAnalyticsId,
    trackingConsentEnabled,
    trackingAllowed,
    profile.countryCode,
  );
  initGoogleAdsTracking(tracking.googleGlobalTagId, trackingAllowed);
  initGoogleAnalytics4Tracking(tracking.googleUniversalAnalyticsId, trackingAllowed);
  initFBPixelTracking(tracking.fbPixelId, trackingAllowed);
  initSnapPixelTracking(tracking.snapPixelId, trackingAllowed);
  initPinterestTagTracking(tracking.pinterestTagId, trackingAllowed);
  initTikTokPixelTracking(tracking.tikTokPixelCode, trackingAllowed);
}

export function sendTrackingSearchEvent(keyword: string): void {
  sendFBPixelTrackingSearchEvent(keyword);
  sendSnapPixelTrackingSearchEvent(keyword);
  sendPinterestTagTrackingSearchEvent(keyword);
  sendGoogleAnalytics4SearchEvent(keyword);
}
