import { Ref } from '@vue/composition-api';
import { configureTracking, startTracking } from '@/app/website/client/logic/tracking/google-global-tag';

export function initTracking(
  trackingId: string | undefined,
  trackingAllowed: Ref<boolean>,
): void {
  if (trackingId == null) {
    return;
  }

  configureTracking(trackingId, trackingAllowed);
  startTracking(trackingId);
}

export default {};
