import { MutationPayload } from 'vuex';
import { State } from '@/app/website/client/store/state';
import { MutationType } from '@/app/website/client/store/mutation/type';

interface SetFocusedTileIdMutationPayload {
  focusedTileId: string | undefined;
}

export interface SetFocusedTileIdMutation extends MutationPayload {
  type: MutationType.SET_FOCUSED_TILE_ID;
  payload: SetFocusedTileIdMutationPayload;
}

export default function setFocusedTileMutation(
  oldState: State,
  data: SetFocusedTileIdMutation,
): State {
  const newState = oldState;
  newState.focusedTileId = data.payload.focusedTileId;
  return newState;
}
