import { useStorage } from '@vueuse/core';
import { computed, onMounted, Ref } from '@vue/composition-api';
import { TileData } from '@/app/shared/client/type/tile';
import { Store } from 'vuex';
import { State } from '@/app/website/client/store/state';
import { Profile } from '@/app/shared/client/type/profile';

const TRACKING_USER_RESPONSE_KEY = 'ec-user-response';
const TRACKING_USER_RESPONSE_ISSUED_AT_KEY = 'ec-user-response-issued-at';
const COUNTRY_CODES_WITH_EXPIRING_USER_RESPONSE = ['IE', 'FR'];
const USER_RESPONSE_EXPIRE_AFTER_SECONDS = 180 * 24 * 3600;

export const ACCEPT_TRACKING_USER_RESPONSE = 'true';
export const DECLINE_TRACKING_USER_RESPONSE = 'false';
type TrackingUserResponse =
  typeof ACCEPT_TRACKING_USER_RESPONSE
  | typeof DECLINE_TRACKING_USER_RESPONSE;

const userResponse = useStorage<TrackingUserResponse | undefined>(
  TRACKING_USER_RESPONSE_KEY,
  undefined,
);
const userResponseReceived = computed(() => userResponse.value !== undefined);

const userResponseIssuedAt = useStorage<number | undefined>(
  TRACKING_USER_RESPONSE_ISSUED_AT_KEY,
  undefined,
);

function setUserResponseExpiration(): void {
  userResponseIssuedAt.value = Math.floor(Date.now() / 1000);
}

function clearUserResponseExpiration(): void {
  userResponseIssuedAt.value = undefined;
}

function createSetUserResponseFunction(
  isPreview: boolean,
): (response: TrackingUserResponse | undefined) => void {
  return (response: TrackingUserResponse | undefined): void => {
    if (isPreview) {
      return;
    }
    userResponse.value = response;

    if (response !== undefined) {
      setUserResponseExpiration();
    }
  };
}

function needCheckUserResponseExpiration(profile: Profile): boolean {
  return COUNTRY_CODES_WITH_EXPIRING_USER_RESPONSE
    .some((countryCode) => countryCode === profile.countryCode);
}

function checkUserResponseExpiration(profile: Profile, isPreview: boolean): void {
  if (!needCheckUserResponseExpiration(profile)) {
    return;
  }

  if (userResponseIssuedAt.value === undefined) {
    return;
  }

  const nowInSeconds = Math.floor(Date.now() / 1000);
  if ((nowInSeconds - USER_RESPONSE_EXPIRE_AFTER_SECONDS) > userResponseIssuedAt.value) {
    const setUserResponseFunction = createSetUserResponseFunction(isPreview);
    setUserResponseFunction(undefined);
    clearUserResponseExpiration();
  }
}

function isTrackingConsentEnabled(tileList: TileData[]): boolean {
  return tileList.some((tile) => tile.type === 'GDPR_BANNER' && tile.visibility);
}

function isTrackingAllowed(
  trackingConsentEnabled: boolean,
): boolean {
  if (!trackingConsentEnabled) {
    return true;
  }

  return userResponse.value === ACCEPT_TRACKING_USER_RESPONSE;
}

export interface UseTrackingConsent {
  readonly userResponse: Ref<TrackingUserResponse | undefined>;
  readonly userResponseReceived: Ref<boolean>;
  readonly trackingConsentEnabled: Ref<boolean>;
  readonly trackingAllowed: Ref<boolean>;
  readonly askWithoutAccent: Ref<boolean>;
  readonly setUserResponse: (response: TrackingUserResponse | undefined) => void;
}

export function useTrackingConsent(
  store: Store<State>,
): UseTrackingConsent {
  const profile = computed(() => store.state.profile);
  const tileList = computed(() => store.state.tileList);
  const trackingConsentEnabled = computed(() => isTrackingConsentEnabled(tileList.value));
  const trackingAllowed = computed(() => isTrackingAllowed(
    trackingConsentEnabled.value,
  ));
  const askWithoutAccent = computed(() => profile.value.countryCode === 'IE');

  onMounted(() => {
    checkUserResponseExpiration(profile.value, store.getters.isPreviewMode);
  });

  return {
    userResponse,
    userResponseReceived,
    trackingConsentEnabled,
    trackingAllowed,
    askWithoutAccent,
    setUserResponse: createSetUserResponseFunction(store.getters.isPreviewMode),
  };
}
