import { RawTileData, TileData } from '@/app/shared/client/type/tile';
import { TileType } from '@/app/shared/client/type/tile-type';
import { GlobalData } from '@/app/shared/client/type/tile/global';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';
import { TileRole } from '@/app/shared/client/type/tile-role';

export function parseRawTileData(rawTileData: RawTileData): TileData {
  let externalContent;
  if (rawTileData.externalContent) {
    externalContent = JSON.parse(rawTileData.externalContent);
  } else {
    externalContent = rawTileData.externalContent;
  }
  return {
    id: rawTileData.id,
    type: rawTileData.type as TileType,
    role: rawTileData.role as TileRole,
    sourceId: rawTileData.sourceId,
    content: rawTileData.content ? JSON.parse(rawTileData.content) : rawTileData.content,
    externalContent,
    design: rawTileData.design ? JSON.parse(rawTileData.design) : rawTileData.design,
    visibility: rawTileData.visibility,
    order: rawTileData.order,
    hasChanges: rawTileData.hasChanges,
  };
}

export function parseRawSiteData(rawTileData?: RawTileData): GlobalData | undefined {
  if (rawTileData === undefined) {
    return undefined;
  }

  const content = isNotNullOrEmpty(rawTileData.content)
    ? JSON.parse(rawTileData.content)
    : rawTileData.content;

  const externalContent = isNotNullOrEmpty(rawTileData.externalContent)
    ? JSON.parse(rawTileData.externalContent)
    : rawTileData.externalContent;

  return {
    id: rawTileData.id,
    type: rawTileData.type as TileType,
    role: rawTileData.role as TileRole,
    sourceId: rawTileData.sourceId,
    content,
    externalContent,
    design: JSON.parse(rawTileData.design),
    visibility: rawTileData.visibility,
    order: rawTileData.order,
    hasChanges: rawTileData.hasChanges,
  };
}
