import { TileData } from '@/app/shared/client/type/tile';
import { Bucket } from '@/app/shared/client/type/bucket';
import { Profile } from '@/app/shared/client/type/profile';
import { GlobalData } from '@/app/shared/client/type/tile/global';
import { ImageInUploadingState } from '@/app/shared/client/type/image-in-uploading-state';

export enum PreviewApiEventType {
  UPDATE_PREVIEW_TILE_LIST,
  UPDATE_PREVIEW_SITE_SETTINGS,
  UPDATE_PREVIEW_PROFILE,
  UPDATE_PREVIEW_CONTEXT,
  UPDATE_PREVIEW_UPLOADING_IMAGE_LIST,
  SAVE_MOBILE_PREVIEW_POSITION,
  RECOVERY_MOBILE_PREVIEW_POSITION,
  SET_FOCUSED_TILE_ID,
}

export interface PreviewApiEventPayload {
  type: PreviewApiEventType;
}

export interface UpdatePreviewTileListPayload extends PreviewApiEventPayload {
  type: PreviewApiEventType.UPDATE_PREVIEW_TILE_LIST;
  payload: { tileList: TileData[] };
}

export interface SetFocusedTileIdPayload extends PreviewApiEventPayload {
  type: PreviewApiEventType.SET_FOCUSED_TILE_ID;
  payload: { focusedTileId: string | undefined };
}

export interface UpdatePreviewContextPayload extends PreviewApiEventPayload {
  type: PreviewApiEventType.UPDATE_PREVIEW_CONTEXT;
  payload: {
    bucketUrls: Record<Bucket, string | undefined>,
    translations: Record<string, string>,
  };
}

export interface UpdatePreviewProfile extends PreviewApiEventPayload {
  type: PreviewApiEventType.UPDATE_PREVIEW_PROFILE;
  payload: { profile: Profile };
}

export interface UpdatePreviewSiteSettings extends PreviewApiEventPayload {
  type: PreviewApiEventType.UPDATE_PREVIEW_SITE_SETTINGS;
  payload: { siteSettings: GlobalData };
}

export interface UpdatePreviewUploadingImageList extends PreviewApiEventPayload {
  type: PreviewApiEventType.UPDATE_PREVIEW_UPLOADING_IMAGE_LIST;
  payload: { imageListInUploadingState: ImageInUploadingState[] };
}

export interface SaveMobilePreviewPosition extends PreviewApiEventPayload {
  type: PreviewApiEventType.SAVE_MOBILE_PREVIEW_POSITION;
}

export interface RecoveryMobilePreviewPosition extends PreviewApiEventPayload {
  type: PreviewApiEventType.RECOVERY_MOBILE_PREVIEW_POSITION;
}

export default {};
